<template>
	<div>
		<fieldset class="form--block" v-if="!hidePark">
			<legend class="sub-header">Ви бажаєте застрахувати більше 4 <abbr title="Транспортних засобів">ТЗ</abbr>?</legend>
			<div class="form--row">
				<input @change="changeParkValue(false)"
					   :checked="park === false"
					   class="form--radio"
					   id="no-park"
					   type="radio"
					   name="park"
					   :value="false">
				<label class="form--label form--label__park" for="no-park">
					<div class="form--icon-wrap">
						<svg class="form--icon form--icon__extra-small-not" aria-hidden="true" viewBox="0 0 510 510">
							<use xlink:href="/Content/img/icons.svg#not-park"></use>
						</svg>
						<p class="label--text">Ні</p>
					</div>
				</label>
				<input @change="changeParkValue(defaultParkPeriod)"
					   :checked="park !== false"
					   class="form--radio"
					   id="yes-park"
					   type="radio"
					   name="park"
					   :value="true">
				<label class="form--label form--label__park" for="yes-park">
					<div class="form--icon-wrap">
						<svg class="form--icon form--icon__extra-small" aria-hidden="true" viewBox="0 0 437 235">
							<use xlink:href="/Content/img/icons.svg#park"></use>
						</svg>
						<p class="label--text">Так</p>
					</div>
				</label>
			</div>
		</fieldset>

		<park-amount v-show="park !== false"></park-amount>
	</div>
</template>

<script>
	import ParkAmount from "./ParkAmount/ParkAmount.vue";
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		data() {
			return {
			}
		},
		computed: {
			...mapGetters("park",{
        park: "park"
			}),
			...mapGetters("user",{
        isLegalEntity: "isLegalEntity"
			}),
			hidePark() {
				return this.isLegalEntity === false
			},
			defaultParkPeriod() {
				return this.park || 5
			}
		},
		methods: {
			...mapActions("park", {
				changeParkValue: "changeParkValue"
			})
		},
		components: {
      ParkAmount
		}
	}
</script>




