<template>
  <fieldset v-show="!hidePrivileges" class="form--block">
    <legend class="sub-header">Права на пільги</legend>

    <fieldset v-show="showDetails" class="form--block">
      <p class="form--text">
        Відповідно до Закону <i>«Про обов'язкове страхування цивільно-правової відповідальності власників наземних транспортних засобів»</i>, пільги <b
        class="form--text__bold">не передбачені</b> для <abbr title="Транспортних засобів">ТЗ</abbr>
        з об'ємом двигуна більше 2500 см³.
      </p>

      <legend class="sub2-header">Уточніть об'єм двигуна</legend>
      <div class="form--row">
        <input v-model="detailVolume"
               value="більше 2500 см³"
               class="form--radio"
               id="more-2500"
               type="radio"
               name="detail-volume">
        <label class="form--label form--label__more-2500" for="more-2500">більше 2500 см³</label>
        <input v-model="detailVolume"
               value="менше 2500 см³"
               class="form--radio"
               id="less-2500"
               type="radio"
               name="detail-volume">
        <label class="form--label form--label__less-2500" for="less-2500">менше 2500 см³</label>
      </div>
    </fieldset>

    <div v-show="!hidePrivilegesBtns">

      <fieldset>
        <legend v-show="showDetails" class="sub2-header">Пільги</legend>
        <div class="form--row">
          <input @change="changePrivilegesValue($event.target.value)"
                 class="form--radio"
                 ref="privilege-no"
                 id="privilege-no"
                 type="radio"
                 name="privilege"
                 :value="false">
          <label class="form--label form--label__privileges" for="privilege-no">
            <div class="form--icon-wrap ">
              <svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 129 129">
                <use xlink:href="/Content/img/icons.svg#no-privilege"></use>
              </svg>
              <p class="label--text">Немає</p>
            </div>
          </label>

          <input @change="changePrivilegesValue($event.target.value)"
                 class="form--radio"
                 ref="privilege-pensioner"
                 id="privilege-pensioner"
                 type="radio"
                 name="privilege"
                 value="Пенсіонер">
          <label class="form--label form--label__privileges" for="privilege-pensioner">
            <div class="form--icon-wrap ">
              <svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true"
                   viewBox="0 0 512.001 512.001">
                <use xlink:href="/Content/img/icons.svg#old"></use>
              </svg>
              <p class="label--text">Пенсіонер</p>
            </div>
          </label>

          <input @change="changePrivilegesValue($event.target.value)"
                 class="form--radio"
                 ref="privilege-veteran"
                 id="privilege-veteran"
                 type="radio"
                 name="privilege"
                 value="Ветеран">
          <label class="form--label form--label__privileges" for="privilege-veteran">
            <div class="form--icon-wrap ">
              <svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 496 496">
                <use xlink:href="/Content/img/icons.svg#veteran"></use>
              </svg>
              <p class="label--text">Ветеран</p>
            </div>
          </label>

          <input @change="changePrivilegesValue($event.target.value)"
                 class="form--radio"
                 ref="privilege-invalid"
                 id="privilege-invalid"
                 type="radio"
                 name="privilege"
                 value="Інвалід другої групи">
          <label class="form--label form--label__privileges" for="privilege-invalid">
            <div class="form--icon-wrap ">
              <svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true" viewBox="0 0 496 496">
                <use xlink:href="/Content/img/icons.svg#disabled"></use>
              </svg>
              <p class="label--text">Інвалід другої групи</p>
            </div>
          </label>

          <input @change="changePrivilegesValue($event.target.value)"
                 class="form--radio"
                 ref="privilege-chernobyl"
                 id="privilege-chernobyl"
                 type="radio"
                 name="privilege"
                 value="Чорнобилець першої групи">
          <label class="form--label form--label__privileges" for="privilege-chernobyl">
            <div class="form--icon-wrap ">
              <svg class="form--icon form--icon__small form--icon__small-not" aria-hidden="true"
                   viewBox="0 0 461.806 461.806">
                <use xlink:href="/Content/img/icons.svg#radiation"></use>
              </svg>
              <p class="label--text">Чорнобилець першої груп</p>
            </div>
          </label>
        </div>
      </fieldset>

      <template v-show="showPrivilegesText">
        <div class="form--text">
          <h5 class="form--header__privileges">Правa на пільги мають особи, у яких:</h5>
          <ul class="form--list">
            <li>об'єм двигуна <abbr title="Транспортного засобу">ТЗ</abbr> до 2500 см³</li>
            <li><abbr title="Транспортний засіб">ТЗ</abbr> належить цій особі на правах власності</li>
            <li>управління <abbr title="Транспортним засобом">ТЗ</abbr> здійснюється <b
              class="form--text__bold">тільки</b> особою, що має право на пільги
            </li>
            <li>на <abbr title="Транспортномк засобі">ТЗ</abbr>
              не здійснюється надання платних послуг з перевезення пасажирів або вантажу
            </li>
          </ul>
          <a href="https://zakon.rada.gov.ua/laws/show/1961-15" target="_blank"
             class="form--link form--link__privileges">Повний закон</a>
        </div>

        <div class="form--text">
          <h5 class="form--header__privileges">Відповідальність за порушення умов страхування</h5>
          <p>У разі порушення вищезазначених умов, винуватець ДТП <b class="form--text__bold">повинен компенсувати 50% від суми страхового відшкодування</b>
            страховій компанії</p>
        </div>
      </template>
    </div>
  </fieldset>
</template>

<script>
  import {mapGetters} from "vuex";
  import {mapActions} from "vuex";

  export default {
    data() {
      return {
        detailVolume: "менше 2500 см³"
      }
    },
    computed: {
      ...mapGetters("privileges", {
        privileges: "privileges"
      }),
      ...mapGetters("user", {
        isLegalEntity: "isLegalEntity"
      }),
      ...mapGetters("transport", {
        transType: "transType",
        transExtraName: "transExtraName"
      }),
      hidePrivileges() {
        return this.isLegalEntity === true ||
          (this.transType === "Легковий автомобіль" && this.transExtraName === "B 4") ||
          this.transType === "Автобус" ||
          this.transType === "Грузовий атомобіль" ||
          this.transType === "Причіп"

      },
      showDetails() {
        return this.transType === "Легковий автомобіль" && this.transExtraName === "B 3"
      },
      hidePrivilegesBtns() {
        return this.detailVolume === "більше 2500 см³"
      },
      showPrivilegesText() {
        return this.privileges
      }
    },
    methods: {
      ...mapActions("privileges", {
        changePrivilegesValue: "changePrivilegesValue"
      }),
      resetPrivileges() {
        this.changePrivilegesValue(false);
      },
      setChecked() {
        for (let key in this.$refs) {
          if (this.$refs[key].value === this.privileges.toString()) {
            this.$refs[key].checked = true;
          }
        }
      }
    },
    mounted() {
      this.setChecked();
    },
    updated() {
      if (this.hidePrivileges || this.hidePrivilegesBtns) {
        this.resetPrivileges();
        this.setChecked();
      }
    }
  }
</script>

<style lang="less">
  .form {
    &--header {
      &__privileges {
        font-size: 25px;
        margin-bottom: 5px;
        @media only screen and (max-width: 576px) {
          line-height: 1;
        }
      }
    }
    &--link {
      &__privileges {
        display: block;
      }
    }
    &--label {
      &__privileges {
        @media only screen and (max-width: 1195px) {
          height: 122px;
        }
        @media only screen and (max-width: 320px) {
          height: 132px;
        }
      }
    }
  }
</style>



