<template>
	<fieldset class="form--block">
		<legend class="sub-header">Кількість <abbr title="Транспортних засобів">ТЗ</abbr></legend>
		<div class="form--row">
			<input @change="changeParkValue($event.target.value)"
				   class="form--radio"
				   ref= "five-cars"
				   id="five-cars"
				   type="radio"
				   name="park-amount"
				   :value="5">
			<label class="form--label " for="five-cars">
				<div class="form--icon-wrap">
					<svg class="form--icon" aria-hidden="true" viewBox="0 0 510 510">
						<use xlink:href="/Content/img/icons.svg#park-5"></use>
					</svg>
					<p class="label--text">5 - 9 включно</p>
				</div>
			</label>

			<input @change="changeParkValue($event.target.value)"
				   class="form--radio"
				   ref= "ten-cars"
				   id="ten-cars"
				   type="radio"
				   name="park-amount"
				   :value="10">
			<label class="form--label " for="ten-cars">
				<div class="form--icon-wrap">
					<svg class="form--icon" aria-hidden="true" viewBox="0 0 510 510">
						<use xlink:href="/Content/img/icons.svg#park-10"></use>
					</svg>
					<p class="label--text">10 - 19 включно</p>
				</div>
			</label>

			<input @change="changeParkValue($event.target.value)"
				   class="form--radio"
				   ref= "twenty-cars"
				   id="twenty-cars"
				   type="radio"
				   name="park-amount"
				   :value="20">
			<label class="form--label " for="twenty-cars">
				<div class="form--icon-wrap">
					<svg class="form--icon" aria-hidden="true" viewBox="0 0 510 510">
						<use xlink:href="/Content/img/icons.svg#park-20"></use>
					</svg>
					<p class="label--text">20 - 100 включно</p>
				</div>
			</label>


		</div>
	</fieldset>
</template>

<script>
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		data() {
			return {
			}
		},
		computed: {
			...mapGetters("park",{
				park: "park"
			}),
			...mapGetters("user",{
        isLegalEntity: "isLegalEntity"
			})
		},
		methods: {
			...mapActions("park", {
				changeParkValue: "changeParkValue"
			}),
			setChecked() {
				for (let key in this.$refs) {
					if(this.$refs[key].value === this.park.toString()) {
						this.$refs[key].checked = true;
					}
				}
			}
		},
		mounted () {
			this.setChecked();
		},
		destroyed() {
			this.changeParkValue(false);
		}
	}
</script>



