<template>
	<fieldset class="form--block">
		<legend class="sub2-header"><abbr title="Транспортний засіб">ТЗ</abbr> підлягає <abbr title="Обов'язковому технічному контролю">ОТК</abbr>?</legend>
		<p class="form--text"> ОТК - обов'язковий технічний контроль. Він включає в себе комплексну перевірку технічного стану транспортного засобу на предмет безпеки його для дорожнього руху та охорони навколишнього середовища. <a href="http://hsc.gov.ua/obov-yazkovij-tehnichnij-kontrol" target="_blank" class="form--link otk--link">Дізнатись чи підлягає мій <abbr title="Транспортний засіб">ТЗ</abbr> <abbr title="Обов'язковому технічому контролю">ОТК</abbr></a></p>
		<div class="form--row">
			<input @change="changeOtkValue(false)"
			:checked="!otk"
			class="form--radio"
			id="not-otk"
			type="radio"
			name="otk">
			<label class="form--label " for="not-otk">
				<div class="form--icon-wrap">
					<svg class="form--icon form--icon__not" aria-hidden="true" viewBox="0 0 480.204 480.204">
						<use xlink:href="/Content/img/icons.svg#not-otk"></use>
					</svg>
					<p class="label--text">Ні</p>
				</div>
			</label>

			<input @change="changeOtkValue(true)"
			:checked="otk"
			class="form--radio"
			id="otk"
			type="radio"
			name="otk">
			<label class="form--label " for="otk">
				<div class="form--icon-wrap">
					<svg class="form--icon" aria-hidden="true" viewBox="0 0 480.204 480.204">
						<use xlink:href="/Content/img/icons.svg#otk"></use>
					</svg>
					<p class="label--text">Так</p>
				</div>
			</label>
		</div>

		<calendar></calendar>
	</fieldset>
</template>

<script>
	import Calendar from "./Calendar/Calendar.vue";
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		data() {
			return {
			}
		},
		computed: {
			...mapGetters("otk",{
				otk: "otk"
			})
		},
		methods: {
			...mapActions("otk", {
				changeOtkValue: "changeOtkValue"
			})
		},
		destroyed() {
			this.changeOtkValue(false);
		},
		components: {
			Calendar
		}
	}

</script>

<style lang="less">
.otk {
	&--link {
		display: block;
	}
}
</style>



